<template>
    <div
      class="auto-complete"
      :class="{
        'auto-complete--active': showDropdown,
        'auto-complete--absolute': absolute
      }"
    >
        <SearchBar
          v-model="value" 
          :place-holder="placeHolder"
          :dropdownActive="showDropdown"
          @focus="searchActive = true" 
          @blur="searchActive = false"
        />
        <div v-if="showDropdown" class="auto-complete__dropdown">
          <slot>
            <ResultsList :list="results" @select-item="selectItem"/>
          </slot>
        </div>
    </div>
</template>
<script>
import SearchBar from '@/components/SearchBar.vue'
import ResultsList from '@/components/ResultList.vue'

export default {
  name: 'AutoComplete',
  components: {
    SearchBar,
    ResultsList
  },
  props: {
    placeHolder: {
      default: 'Search...',
      type: String
    },
    absolute: {
        type: Boolean,
        default: false,
    },
    suggestAll: {
        type: Boolean,
        default: false,
    },
    options: {
        type: Array,
        default: () => [],
    },
  },
  data() {
    return {
      value: "",
      searchActive: false,
    }
  },
  computed: {
    hasValue() {
      return this.value && this.value.length > 0;
    },
    showDropdown() {
      if (this.suggestAll) {
        return this.searchActive || this.hasValue;
      } else {
        return this.hasValue;
      }
    },
    results() {
      if (this.hasValue && this.options.length > 0) {
        return this.options.filter((item) => {
          return item.toLowerCase().includes(this.value.toLowerCase());
        });
      } else {
        if (this.suggestAll) {
          return this.options;
        } else {
          return []
        }
      }
    }
  },
  methods: {
    selectItem(val) {
      this.value = val;
    }
  }
}
</script>
<style lang="scss" scoped>
@import "@/styles/scss/_variables.scss";

.auto-complete {
  position: relative;
  // border: 1px solid transparent;

  &.auto-complete--absolute {
    .auto-complete__dropdown {
      position: absolute;
      width: 100%;
      z-index: 2;
    }
  }

  &.auto-complete--active {
    background: $background;
    border-top-left-radius: 20px;
    border-top-right-radius: 20px;
  }
  .auto-complete__dropdown {
    background: $background;
    padding: 10px 0;
    box-shadow: 0px 8px 10px 1px rgba(0, 0, 0, 0.25);

    // 0px 8px 4px 5px
    padding-bottom: 10px;
    border-bottom-right-radius: 20px;
    border-bottom-left-radius: 20px;
  }
}
</style>
